import Vue from "vue";
import App from "./App.vue";
import router from "./router/router";
import store from "./store/store";
import "./registerServiceWorker"; // pwa
import "@/common/styles/base.css"; // 
import "@/common/js/rem.js"; // rem
import "../public/js/api.js"; // apiCloud-api
import * as method from "@/common/js/mixin.js"; // 
import API from "@/server/apis.js"; // api
import VConsole from "vconsole";
import Navigation from "vue-navigation";
// vant,
// import Vant from 'vant';
// import 'vant/lib/index.css';
// Vue.use(Vant);
Vue.config.productionTip = false;
// 
Vue.prototype.$METHOD = method;
// API
Vue.prototype.$SERVER = API;
// 
Vue.use(Navigation, {
  router,
  moduleName: "navigation",
  keyName: "AS"
});
// console()
new VConsole();
// apiIDapp
if (window.api && window.api.appId === "A6006996353979") {
  window.apiready = function() {
    new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount("#app");
  };
} else {
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
}
