/* eslint-disable */
/**
 * touchmove
 * @param {Element} el
 */
export const openScroll = function (el) {
  el.addEventListener(
    "touchstart",
    function () {
      var top = el.scrollTop;
      var totalScroll = el.scrollHeight;
      var currentScroll = top + el.offsetHeight;
      if (top === 0) {
        el.scrollTop = 1;
      } else if (currentScroll === totalScroll) {
        el.scrollTop = top - 1;
      }
    }, {
      passive: true
    }
  );
  el.addEventListener(
    "touchmove",
    function (evt) {
      if (el.offsetHeight < el.scrollHeight) {
        evt._isScroller = true;
      }
    }, {
      passive: true
    }
  );
};
/**
 *
 * @param {Number} len uuid
 * @param {Number} radix uuid
 * @param {String} typeid uuid
 */
export const uuid = function (len, radix, typeid) {
  var chars = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz".split(
    ""
  );
  var uuid = [];
  var typeid = typeid || "";
  var radix = radix || chars.length;
  var i;
  if (len) {
    for (i = 0; i < len; i++) uuid[i] = chars[0 | (Math.random() * radix)];
  } else {
    var r;
    uuid[8] = uuid[13] = uuid[18] = uuid[23] = "-";
    uuid[14] = "4";
    for (i = 0; i < 36; i++) {
      if (!uuid[i]) {
        r = 0 | (Math.random() * 16);
        uuid[i] = chars[i === 19 ? (r & 0x3) | 0x8 : r];
      }
    }
  }
  return typeid + "-" + uuid.join("");
};

/**
 * urlJSON
 * @param {url}} url
 */
export const parseQueryString = function (url) {
  var regUrl = /^[^?]+\?([\w\W]+)$/;
  var regPara = /([^&=]+)=([\w\W]*?)(&|$|#)/g;
  var arrUrl = regUrl.exec(url);
  var ret = {};
  if (arrUrl && arrUrl[1]) {
    var strPara = arrUrl[1];
    var result;
    while ((result = regPara.exec(strPara)) != null) {
      ret[result[1]] = result[2];
    }
  }
  return ret;
};
/**
 * 
 * @param {Number} dateTimeStamp 
 */
export const timeSwitchString = function (dateTimeStamp) {
  // dateTimeStamp100013
  var minute = 1000 * 60; // 
  var hour = minute * 60;
  var day = hour * 24;
  var week = day * 7;
  // var halfamonth = day * 15
  var month = day * 30;
  var now = new Date().getTime(); // 
  var diffValue = now - dateTimeStamp; // 
  var result;
  if (diffValue < 0) {
    return;
  }
  var minC = diffValue / minute; // 
  var hourC = diffValue / hour;
  var dayC = diffValue / day;
  var weekC = diffValue / week;
  var monthC = diffValue / month;
  if (monthC >= 1 && monthC <= 3) {
    result = " " + parseInt(monthC) + "";
  } else if (weekC >= 1 && weekC <= 3) {
    result = " " + parseInt(weekC) + "";
  } else if (dayC >= 1 && dayC <= 6) {
    result = " " + parseInt(dayC) + "";
  } else if (hourC >= 1 && hourC <= 23) {
    result = " " + parseInt(hourC) + "";
  } else if (minC >= 1 && minC <= 59) {
    result = " " + parseInt(minC) + "";
  } else if (diffValue >= 0 && diffValue <= minute) {
    result = "";
  } else {
    var datetime = new Date();
    datetime.setTime(dateTimeStamp);
    var Nyear = datetime.getFullYear();
    var Nmonth =
      datetime.getMonth() + 1 < 10 ?
      "0" + (datetime.getMonth() + 1) :
      datetime.getMonth() + 1;
    var Ndate =
      datetime.getDate() < 10 ? "0" + datetime.getDate() : datetime.getDate();
    // var Nhour = datetime.getHours() < 10 ? '0' + datetime.getHours() : datetime.getHours()
    // var Nminute = datetime.getMinutes() < 10 ? '0' + datetime.getMinutes() : datetime.getMinutes()
    // var Nsecond = datetime.getSeconds() < 10 ? '0' + datetime.getSeconds() : datetime.getSeconds()
    result = Nyear + "-" + Nmonth + "-" + Ndate;
  }
  return result;
};

/**
 * 
 * '-''/',iPhone
 * @param {String} setEndTime 2018/08/27 18:00
 */
export const countDownTime = function (setEndTime) {
  let endTime = new Date(setEndTime).getTime();
  let time = new Date().getTime();

  if (endTime <= time) {
    return [];
  } else {
    let HsecondNum = endTime - time;
    // let day = Math.floor(HsecondNum / (24 * 3600 * 1000))

    let dd = Math.floor(HsecondNum % (24 * 3600 * 1000));
    let hours = Math.floor(HsecondNum / (3600 * 1000));

    let minutesNum = dd % (3600 * 1000);
    let minutes = Math.floor(minutesNum / (60 * 1000));

    let secondNum = dd % (60 * 1000);
    let second = Math.floor(secondNum / 1000);
    return [hours, minutes, second];
  }
};

// 
export const isWeixin = () => {
  let ua = navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) == "micromessenger") { //eslint-disable-line
    return true;
  } else {
    return false;
  }
};
// QQ
export const isQQ = () => {
  let ua = navigator.userAgent.toLowerCase();
  return !!ua.match(/mqqbrowser|qzone|qqbrowser/i);
};
// 
export const isAndroid = () => {
  let ua = navigator.userAgent;
  return ua.indexOf("Android") > -1 || ua.indexOf("Linux") > -1;
};
// ios
export const isIos = () => {
  let ua = navigator.userAgent;
  return !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
};

/**
 * localStorage
 */
export const setStore = (name, content) => {
  if (!name) return;
  if (typeof content !== "string") {
    content = JSON.stringify(content);
  }
  window.localStorage.setItem(name, content);
};

/**
 * localStorage
 */
export const getStore = name => {
  if (!name) return;
  return window.localStorage.getItem(name);
};

/**
 * localStorage
 */
export const removeStore = name => {
  if (!name) return;
  window.localStorage.removeItem(name);
};

/**
 * cookie
 */
export const setCookie = (objName, objValue, objHours = 30) => {
  var str = objName + "=" + escape(objValue);
  if (objHours != null) {
    var date = new Date();
    var ms = objHours * 3600 * 1000 * 24;
    date.setTime(date.getTime() + ms);
    str += "; expires=" + date.toGMTString();
  }
  document.cookie = str;
};

/**
 * cookie
 */
export const getCookie = objName => {
  var search = objName + "=";
  if (document.cookie.length > 0) {
    var offset = document.cookie.indexOf(search);
    if (offset !== -1) {
      offset += search.length;
      var end = document.cookie.indexOf(";", offset);
      if (end === -1) end = document.cookie.length;
      return unescape(document.cookie.substring(offset, end));
    } else {
      return "";
    }
  }
};
/**
 * cookie
 */
export const delCookie = name => {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null) {
    document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
  }
};
/**
 * cookie
 */
export const clearCookie = () => {
  var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
  if (keys) {
    for (var i = keys.length; i--;) {
      document.cookie = keys[i] + "=0;expires=" + new Date(0).toUTCString();
    }
  }
};
export const hasClass = (obj, cls) => {
  return obj.className.match(new RegExp("(\\s|^)" + cls + "(\\s|$)"));
};

export const addClass = (obj, cls) => {
  if (!this.hasClass(obj, cls)) obj.className += " " + cls;
};

export const removeClass = (obj, cls) => {
  if (hasClass(obj, cls)) {
    var reg = new RegExp("(\\s|^)" + cls + "(\\s|$)");
    obj.className = obj.className.replace(reg, " ");
  }
};
// 
export const IdCodeValid = function (code) {
  // 
  // 1518
  // 
  var city = {
    11: "",
    12: "",
    13: "",
    14: "",
    15: "",
    21: "",
    22: "",
    23: " ",
    31: "",
    32: "",
    33: "",
    34: "",
    35: "",
    36: "",
    37: "",
    41: "",
    42: " ",
    43: "",
    44: "",
    45: "",
    46: "",
    50: "",
    51: "",
    52: "",
    53: "",
    54: " ",
    61: "",
    62: "",
    63: "",
    64: "",
    65: "",
    71: "",
    81: "",
    82: "",
    91: " "
  };
  var row = {
    pass: true,
    msg: ""
  };
  if (
    !code ||
    !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|[xX])$/.test(
      code
    )
  ) {
    row = {
      pass: false,
      msg: ""
    };
  } else if (!city[code.substr(0, 2)]) {
    row = {
      pass: false,
      msg: ""
    };
  } else {
    // 18
    if (code.length === 18) {
      code = code.split("");
      // (aiWi)(mod 11)
      // 
      var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
      // 
      var parity = [1, 0, "X", 9, 8, 7, 6, 5, 4, 3, 2];
      var sum = 0;
      var ai = 0;
      var wi = 0;
      for (var i = 0; i < 17; i++) {
        ai = code[i];
        wi = factor[i];
        sum += ai * wi;
      }
      if (parity[sum % 11] != code[17].toUpperCase()) {
        row = {
          pass: false,
          msg: ""
        };
      }
    }
  }
  return row;
};